@import "mixins.scss";

@import "variables.scss";

body {
  h1 {
    font-family: "GalienBold", Arial;
    font-size: 64px;
    line-height: 74px;
    letter-spacing: 0.04em;
    color: $font-color;
    margin-bottom: 32px;
  }

  h2 {
    font-family: "GilroySemiBold", Arial;
    font-size: 36px;
    line-height: 46px;
    letter-spacing: 0.05em;
    color: #316099;
  }

  h3 {
    font-family: "GilroySemiBold", Arial;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.05em;
    color: $font-color;
    margin-bottom: 16px;
  }

  .__text {
    font-family: "GilroyRegular", Arial;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.05em;
    color: #1f3f68;
    opacity: 0.6;
  }
}

.header {
  max-width: 1200px;
  width: 100%;
  padding: 40px 5px 45px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .logo {
    width: 48px;
    height: 48px;
    position: relative;

    &__ball {
      position: relative;
      height: 32px;
      width: 32px;
      background-color: #316099;
      border-radius: 100%;
    }

    &__simbol {
      position: absolute;
      transform: translateX(50%);
      top: 0;
      z-index: 1;
      background-color: #ffffff;

      height: 32px;
      width: 32px;
      border-radius: 100%;
      border: 2px solid #316099;
      font-family: GilroyBold;
      text-align: center;
      font-size: 20px;
      line-height: 28px;
      color: #316099;
      vertical-align: middle;
    }
  }

  .nav__button {
    position: relative;
    display: block;
    border: none;
    width: 32px;
    background: none;
    top: -10px;

    span {
      @include burger;

      &::before {
        content: "";
        @include burger;
        transform: translateY(-5px);
      }
      &::after {
        content: "";
        @include burger;
        transform: translateY(5px);
      }
    }
  }
}

.intro {
  // background: linear-gradient(
  //   0deg,
  //   #98c3e8 8.84%,
  //   rgba(152, 195, 232, 0) 31.12%
  // );
  // opacity: 0.2;
  // box-shadow: inset 0 -100px 0 rgba(16, 112, 177, 0.2); (background обгрызает тень)

  &-container {
    max-width: 1366px;
    width: 100%;
    margin: 0 auto;
    padding: 101px 0 0 212px;

    background-image: url(../img/intro/illustration.jpg);
    background-repeat: no-repeat;
    background-position: right 5% top 50%;

    &-text {
      max-width: 520px;
    }
    .__text {
      margin-bottom: 71px;
    }
    .intro__button {
      font-family: "GilroyRegular";
      font-size: 16px;
      line-height: 26px;
      text-align: center;
      letter-spacing: 0.04em;
      color: #ffffff;

      background: #5a98d0;
      box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.2);
      border: none;
      border-radius: 10px;
      padding: 15px 48px;
      margin-bottom: 190px;
    }
  }
}

.feature {
  padding: 180px 0 91px;
  box-shadow: 0px -200px 80px 0px rgba(152, 195, 232, 0.2);

  &-container {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      ". item-2"
      "item-1 item-2"
      "item-1 item-3"
      ". item-3";
    gap: 100px;

    position: relative;
    max-width: 940px;
    margin: 0 auto;
    padding: 5px;

    ._item-1 {
      grid-area: item-1;
      margin-left: 80px;
    }

    ._item-2 {
      grid-area: item-2;
      padding-top: 236px;
    }

    ._item-3 {
      grid-area: item-3;
    }

    &-title {
      position: absolute;
      max-width: 360px;
      padding: 5px;
    }

    &-card {
      position: relative;
      max-width: 380px;
      background: #ffffff;
      box-shadow: 5px 20px 50px rgba(16, 112, 177, 0.2);
      border-radius: 10px;
      padding: 48px 47px 48px;

      &__img {
        position: relative;
        left: 10%;
        margin-bottom: 56px;
      }

      ._absolute {
        position: absolute;
        top: -20px;
        left: 65px;
      }

      .__text {
        margin-bottom: 32px;
      }

      .__link {
        @include link;

        &__img {
          padding-left: 28px;
          transform: translateY(3px);
        }
      }
    }
  }
}

.finished {
  padding: 91px 5px 192px;

  .container {
    max-width: 950px;
    margin: 0 auto;
    width: 100%;
    padding: 5px;

    &-text {
      display: flex;
      max-width: 840px;
      width: 100%;
      gap: 90px;
      margin-bottom: 96px;

      .container-title {
        max-width: 230px;
        width: 100%;
        padding: 5px;

        h2 {
          color: #1f3f68;
        }
      }
    }

    &-card {
      display: flex;
      flex-wrap: wrap;
      gap: 96px;

      ._marketplace__background {
        background: linear-gradient(
          308.85deg,
          rgba(66, 165, 245, 0.4) -1.71%,
          rgba(21, 101, 192, 0.6) 74.54%
        );
      }

      ._board__background {
        background: linear-gradient(
          306.48deg,
          rgba(66, 165, 245, 0.4) 0%,
          rgba(21, 101, 192, 0.6) 97.81%
        );
      }

      &-item {
        position: relative;
        overflow: hidden;
        border-radius: 10px;

        &-text {
          position: absolute;
          top: 48px;
          right: 48px;

          .card__title {
            @include card__title;
          }

          .card__subtitle {
            @include card__subtitle;
            text-align: end;
          }
        }

        ._position {
          left: 32px;
          top: 24px;

          p {
            text-align: start;
          }
        }
      }
    }

    ._width {
      max-width: 422px;
      border-radius: 10px;
    }
    ._size__img {
      position: relative;
      z-index: -1;
      height: 100%;
      width: 100%;
      // хотелось бы понять, когда мы вставляем картинку в контейнер, какие свойства мы должны к ней применить
    }
  }
}

.client {
  padding: 91px 5px;
  background: #5a98d0;
  border-radius: 200px 0px 0px 0px;

  .container {
    max-width: 950px;
    margin: 0 auto;
    width: 100%;
    padding: 10px;

    &-text {
      display: flex;
      max-width: 840px;
      width: 100%;
      gap: 90px;
      margin-bottom: 111px;

      .container-title {
        max-width: 230px;
        width: 100%;

        .__title {
          color: #ffffff;
        }
      }
      .__text {
        color: #ffffff;
      }
    }

    &-clients {
      max-width: 950px;
      width: 100%;
      margin: 0 auto 125px;
      padding: 5px;
      display: flex;
      flex-wrap: wrap;

      &-cells {
        width: 25%;
        font-family: "GilroySemiBold";
        font-size: 20px;
        line-height: 46px;
        letter-spacing: 0.02em;
        color: #f2f2f2;
        text-align: center;
        padding: 41px 40px;

        &:nth-last-child(1) {
          background: #1f3f68;
          background: rgba(0, 0, 0, 0.2);
        }
        @include nth-child__border;
      }
    }
  }
}

.joinus {
  background: #316099;
  border-radius: 0px 200px 0px 0px;
  margin-right: 96px;
}
